.BaAppBar .MuiTabs-indicator {
    display: none;
}

.BaAppBar .MuiTab-root {
    border: 1px solid #999999;
    border-left: none;
    border-bottom: none;
    margin-top: 3px;
    background: #cecece;
    outline: none;
}

.BaAppBar .Mui-selected {
    margin-top: 0px;
    border-top: 3px solid #E67839;
    background: #ffffff;
    padding: 5px 20px 15px 20px;
    color: #E67839;
    font-weight: bold;
}